.popup_inner {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  -o-object-fit: cover;
     object-fit: cover;
  width: 250px;
  height: 250px;
  margin: auto;
  margin-top: 300px;
  background: rgba(0,0,0,0);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border: 5px solid blue;
}

.popup-content {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.popover {
  top: -25%;
  left: 5%;
}

.popup-img {
  width: 200px;
  height: 200px;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 0 auto;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5)
}



